.App {
  text-align: center; }

/* Define a variable at the top level of the cascade so that it is easily accessible to react */
:root {
  --primary-light: #9be7ff;
  --primary-main: #64b5f6;
  --primary-dark: #2286c3;
  --primary-contrast-text: #263238;
  --secondary-light: #ffe97d;
  --secondary-main: #ffb74d;
  --secondary-dark: #c88719;
  --secondary-contrast-text: #263238; }

.minefield {
  --columns: 8;
  --content-width: 96vw;
  --gutter: 1vw;
  --row-size: calc(
    (var(--content-width) - (var(--gutter) * (var(--columns) - 1))) /
      var(--columns)
  );
  display: grid;
  width: 100%;
  margin: calc(2vw + 64px) auto 2vw auto;
  max-width: var(--content-width);
  grid-template-columns: repeat(var(--columns), 1fr);
  grid-auto-rows: var(--row-size);
  grid-column-gap: var(--gutter);
  grid-row-gap: var(--gutter);
  justify-items: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: calc(70vw / var(--columns));
  line-height: var(--row-size); }
  .minefield > div svg {
    margin: auto;
    height: 100%; }

.scene {
  width: 100%;
  height: 100%;
  -webkit-perspective: calc(var(--row-size) * 3);
          perspective: calc(var(--row-size) * 3); }

.card {
  width: 100%;
  height: 100%;
  position: relative;
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  -o-transition: transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d; }
  .card .front {
    background: var(--secondary-light);
    color: var(--primary-dark); }
  .card .back {
    color: var(--secondary-main);
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg); }
  .card.reveal {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg); }
  .card.delay-0 {
    -webkit-transition-delay: 0ms;
         -o-transition-delay: 0ms;
            transition-delay: 0ms; }
  .card.delay-1 {
    -webkit-transition-delay: 100ms;
         -o-transition-delay: 100ms;
            transition-delay: 100ms; }
  .card.delay-2 {
    -webkit-transition-delay: 200ms;
         -o-transition-delay: 200ms;
            transition-delay: 200ms; }
  .card.delay-3 {
    -webkit-transition-delay: 300ms;
         -o-transition-delay: 300ms;
            transition-delay: 300ms; }
  .card.delay-4 {
    -webkit-transition-delay: 400ms;
         -o-transition-delay: 400ms;
            transition-delay: 400ms; }
  .card.delay-5 {
    -webkit-transition-delay: 500ms;
         -o-transition-delay: 500ms;
            transition-delay: 500ms; }

.card-face {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  border-radius: 50%;
  height: 100%;
  position: absolute;
  width: 100%; }

.pulse {
  -webkit-animation: pulsate 1s ease-in-out;
          animation: pulsate 1s ease-in-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite; }

@-webkit-keyframes pulsate {
  0% {
    width: 60%;
    height: 60%;
    margin: 20%;
    opacity: 0.6;
    stroke-width: 3; }
  50% {
    width: 80%;
    height: 80%;
    margin: 10%;
    opacity: 1;
    stroke-width: 5; }
  100% {
    width: 60%;
    height: 60%;
    margin: 20%;
    opacity: 0.6;
    stroke-width: 3; } }

@keyframes pulsate {
  0% {
    width: 60%;
    height: 60%;
    margin: 20%;
    opacity: 0.6;
    stroke-width: 3; }
  50% {
    width: 80%;
    height: 80%;
    margin: 10%;
    opacity: 1;
    stroke-width: 5; }
  100% {
    width: 60%;
    height: 60%;
    margin: 20%;
    opacity: 0.6;
    stroke-width: 3; } }



